          </el-button>
        </div>
      </template>
      
      <!-- 商户统计概览 -->
      <div class="merchant-overview">
        <el-row :gutter="20">
          <el-col :span="6">
            <div class="merchant-stat-card">
              <div class="stat-icon total">
                <el-icon><User /></el-icon>
              </div>
              <div class="stat-content">
                <h3>{{ merchantOverview.totalMerchants || 0 }}</h3>
                <p>总商户数</p>
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="merchant-stat-card">
              <div class="stat-icon normal">
                <el-icon><Check /></el-icon>
              </div>
              <div class="stat-content">
                <h3>{{ merchantOverview.normalMerchants || 0 }}</h3>
                <p>正常商户</p>
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="merchant-stat-card">
              <div class="stat-icon slow">
                <el-icon><Clock /></el-icon>
              </div>
              <div class="stat-content">
                <h3>{{ merchantOverview.slowMerchants || 0 }}</h3>
                <p>慢响应商户</p>
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="merchant-stat-card">
              <div class="stat-icon circuit">
                <el-icon><Warning /></el-icon>
              </div>
              <div class="stat-content">
                <h3>{{ merchantOverview.circuitBreakerMerchants || 0 }}</h3>
                <p>熔断商户</p>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>

      <!-- 商户详细列表 -->
      <div class="merchant-details">
        <el-table :data="merchantStats" stripe>
          <el-table-column prop="merchant_name" label="商户名称" min-width="150">
            <template #default="{ row }">
              {{ row.merchant_name || row.merchant_key }}
            </template>
          </el-table-column>
          <el-table-column prop="merchant_key" label="商户标识" min-width="120" />
          <el-table-column prop="notify_url" label="回调地址" min-width="200" show-overflow-tooltip>
            <template #default="{ row }">
              <div class="callback-url-container">
                <span class="callback-url" @click="copyCallbackUrl(row.notify_url)" :title="row.notify_url">
                  {{ row.notify_url || '-' }}
                </span>
                <el-button 
                  v-if="row.notify_url"
                  @click="copyCallbackUrl(row.notify_url)"
                  type="text" 
                  size="small"
                  class="copy-btn"
                  title="复制回调地址"
                >
                  <el-icon><DocumentCopy /></el-icon>
                </el-button>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="status" label="状态" width="100">
            <template #default="{ row }">
              <el-tag :type="getMerchantStatusType(row.status)">
                {{ getMerchantStatusText(row.status) }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="failed_requests" label="失败数" width="80" />
          <el-table-column prop="avg_response_time" label="平均响应时间" width="120">
            <template #default="{ row }">
              {{ row.avg_response_time || 0 }}ms
            </template>
          </el-table-column>
          <el-table-column label="操作" width="120">
            <template #default="{ row }">
              <el-button 
                v-if="row.status === 'circuit_breaker'"
                @click="resetCircuitBreaker(row.merchant_key)"
                type="warning"
                size="small"
              >
                重置熔断器
              </el-button>
              <el-button 
                @click="viewMerchantDetail(row)"
                type="primary"
                size="small"
              >
                查看详情
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>

    <!-- 通知日志 -->
    <el-card class="notify-logs-card">
      <template #header>
        <div class="card-header">
          <span>通知日志</span>
          <div class="header-actions">
            <el-button @click="refreshNotifyLogs" :loading="notifyLogsLoading">
              <el-icon><Refresh /></el-icon>
              刷新
            </el-button>
            <el-button @click="showFilters = !showFilters">
              <el-icon><Filter /></el-icon>
              筛选
            </el-button>
          </div>
        </div>
      </template>

      <!-- 筛选条件 -->
      <div v-if="showFilters" class="filters">
        <el-form :model="filters" inline>
          <el-form-item label="订单号">
            <el-input v-model="filters.order_no" placeholder="请输入订单号" clearable />
          </el-form-item>
          <el-form-item label="状态">
            <el-select v-model="filters.status" placeholder="请选择状态" clearable>
              <el-option label="全部" value="" />
              <el-option label="成功" value="success" />
              <el-option label="失败" value="failed" />
              <el-option label="处理中" value="pending" />
            </el-select>
          </el-form-item>
          <el-form-item label="商户标识">
            <el-input v-model="filters.merchant_key" placeholder="请输入商户标识" clearable />
          </el-form-item>
          <el-form-item label="开始时间">
            <el-date-picker
              v-model="filters.start_time"
              type="datetime"
              placeholder="选择开始时间"
              format="YYYY-MM-DD HH:mm:ss"
              value-format="YYYY-MM-DD HH:mm:ss"
            />
          </el-form-item>
          <el-form-item label="结束时间">
            <el-date-picker
              v-model="filters.end_time"
              type="datetime"
              placeholder="选择结束时间"
              format="YYYY-MM-DD HH:mm:ss"
              value-format="YYYY-MM-DD HH:mm:ss"
            />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="searchNotifyLogs">
              <el-icon><Search /></el-icon>
              搜索
            </el-button>
            <el-button @click="resetFilters">
              <el-icon><RefreshLeft /></el-icon>
              重置
            </el-button>
          </el-form-item>
        </el-form>
      </div>

      <!-- 日志表格 -->
      <el-table :data="notifyLogs" v-loading="notifyLogsLoading" stripe>
        <el-table-column prop="id" label="ID" width="80" />
        <el-table-column prop="order_no" label="订单号" width="200" />
        <el-table-column prop="merchant_order_no" label="商户订单号" width="200" />
        <el-table-column prop="notify_url" label="回调地址" show-overflow-tooltip />
        <el-table-column prop="status" label="状态" width="100">
          <template #default="{ row }">
            <el-tag :type="getNotifyStatusType(row.status)">
              {{ getNotifyStatusText(row.status) }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="retry_count" label="重试次数" width="100" />
        <el-table-column prop="http_code" label="HTTP状态码" width="120" />
        <el-table-column prop="response_data" label="响应数据" show-overflow-tooltip />
        <el-table-column prop="created_at" label="创建时间" width="180" />
      </el-table>

      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          v-model:current-page="pagination.currentPage"
          v-model:page-size="pagination.pageSize"
          :page-sizes="[10, 20, 50, 100]"
          :total="pagination.total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>

  </div>
</template>

<script setup>
import { ref, reactive, onMounted, onUnmounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { 
  Check, 
  SuccessFilled, 
  Close, 
  TrendCharts, 
  Refresh, 
  Filter, 
  Search, 
  RefreshLeft,
  User,
  Clock,
  Warning,
  DocumentCopy,
  Bell
} from '@element-plus/icons-vue'

import { 
  getMerchantStats, 
  getNotifyLogs, 
  resetCircuitBreaker as resetCircuitBreakerApi,
  getQueueStatus,
} from '@/api/monitor'

// 响应式数据
const merchantStats = ref([])
const notifyLogs = ref([])
const queueStatus = ref({
  // 基础队列数据
  notifyQueue: 0,
  retryQueue: 0,
  delayedQueue: 0,
  lastUpdate: ''
})
const merchantOverview = ref({
  totalMerchants: 0,
  normalMerchants: 0,
  slowMerchants: 0,
  circuitBreakerMerchants: 0
})


// 时间范围选择
const selectedTimeRange = ref('1m') // 默认1分钟

// 加载状态
const merchantStatsLoading = ref(false)
const notifyLogsLoading = ref(false)
const queueStatusLoading = ref(false)

// 筛选条件
const showFilters = ref(false)
const filters = reactive({
  order_no: '',
  status: '',
  merchant_key: '',
  start_time: '',
  end_time: ''
})

// 分页
const pagination = reactive({
  currentPage: 1,
  pageSize: 20,
  total: 0
})

// 定时器
let refreshTimer = null


// 处理时间范围变化
const handleTimeRangeChange = () => {
  // 重新获取所有数据
  refreshMerchantStats()
  refreshNotifyLogs()
  refreshQueueStatus()
}

// 获取时间范围文本
const getTimeRangeText = () => {
  const timeRangeMap = {
    '5s': '最近5秒',
    '1m': '最近1分钟',
    '5m': '最近5分钟',
    '10m': '最近10分钟',
    '1h': '最近1小时',
    '1d': '最近1天'
  }
  return timeRangeMap[selectedTimeRange.value] || '最近1分钟'
}

// 获取商户统计
const refreshMerchantStats = async () => {
  merchantStatsLoading.value = true
  try {
    const params = {
      time_range: selectedTimeRange.value
    }
    const response = await getMerchantStats(params)
    if (response.code === 200) {
      // 修复：正确提取merchants数组数据
      merchantStats.value = response.data.merchants || []
      // 计算商户概览统计
      calculateMerchantOverview()
    }
  } catch (error) {
    ElMessage.error('获取商户统计失败')
    console.error('获取商户统计失败:', error)
  } finally {
    merchantStatsLoading.value = false
  }
}

// 计算商户概览统计
const calculateMerchantOverview = () => {
  // 添加类型检查，确保merchantStats.value是数组
  if (!Array.isArray(merchantStats.value)) {
    console.warn('merchantStats.value 不是数组:', merchantStats.value)
    merchantStats.value = []
  }
  
  const stats = {
    totalMerchants: merchantStats.value.length,
    normalMerchants: 0,
    slowMerchants: 0,
    circuitBreakerMerchants: 0
  }
  
  merchantStats.value.forEach(merchant => {
    switch (merchant.status) {
      case 'normal':
        stats.normalMerchants++
        break
      case 'slow':
        stats.slowMerchants++
        break
      case 'circuit_breaker':
        stats.circuitBreakerMerchants++
        break
    }
  })
  
  merchantOverview.value = stats
}


// 获取队列状态
const refreshQueueStatus = async () => {
  queueStatusLoading.value = true
  try {
    const response = await getQueueStatus()
    if (response.code === 200) {
      // 直接更新队列状态数据
      queueStatus.value = {
        notifyQueue: response.data.notifyQueue || 0,
        retryQueue: response.data.retryQueue || 0,
        delayedQueue: response.data.delayedQueue || 0,
        lastUpdate: response.data.lastUpdate || ''
      }
      console.log('队列状态更新:', queueStatus.value)
    }
  } catch (error) {
    ElMessage.error('获取队列状态失败')
    console.error('获取队列状态失败:', error)
  } finally {
    queueStatusLoading.value = false
  }
}



// 获取通知日志
const refreshNotifyLogs = async () => {
  notifyLogsLoading.value = true
  try {
    const params = {
      page: pagination.currentPage,
      limit: pagination.pageSize,
      time_range: selectedTimeRange.value,
      ...filters
    }
    const response = await getNotifyLogs(params)
    if (response.code === 200) {
      notifyLogs.value = response.data.list
      pagination.total = response.data.total
    }
  } catch (error) {
    ElMessage.error('获取通知日志失败')
    console.error('获取通知日志失败:', error)
  } finally {
    notifyLogsLoading.value = false
  }
}


// 搜索通知日志
const searchNotifyLogs = () => {
  pagination.currentPage = 1
  refreshNotifyLogs()
}

// 重置筛选条件
const resetFilters = () => {
  Object.assign(filters, {
    order_no: '',
    status: '',
    merchant_key: '',
    start_time: '',
    end_time: ''
  })
  searchNotifyLogs()
}

// 重置熔断器
const resetCircuitBreaker = async (merchantKey) => {
  try {
    await ElMessageBox.confirm('确定要重置该商户的熔断器吗？', '确认重置', {
      type: 'warning'
    })
    
    const response = await resetCircuitBreakerApi({ merchant_key: merchantKey })
    if (response.code === 200) {
      ElMessage.success('熔断器重置成功')
      refreshMerchantStats()
    } else {
      ElMessage.error(response.message || '重置失败')
    }
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('重置熔断器失败')
      console.error('重置熔断器失败:', error)
    }
  }
}


// 分页处理
const handleSizeChange = (size) => {
  pagination.pageSize = size
  pagination.currentPage = 1
  refreshNotifyLogs()
}

const handleCurrentChange = (page) => {
  pagination.currentPage = page
  refreshNotifyLogs()
}

// 状态类型映射
const getMerchantStatusType = (status) => {
  const statusMap = {
    'normal': 'success',
    'slow': 'warning',
    'circuit_breaker': 'danger'
  }
  return statusMap[status] || 'info'
}

const getMerchantStatusText = (status) => {
  const statusMap = {
    'normal': '正常',
    'slow': '慢响应',
    'circuit_breaker': '熔断'
  }
  return statusMap[status] || '未知'
}

const getNotifyStatusType = (status) => {
  const statusMap = {
    'success': 'success',
    'failed': 'danger',
    'pending': 'warning'
  }
  return statusMap[status] || 'info'
}

const getNotifyStatusText = (status) => {
  const statusMap = {
    'success': '成功',
    'failed': '失败',
    'pending': '处理中'
  }
  return statusMap[status] || '未知'
}





// 查看商户详情
const viewMerchantDetail = (merchant) => {
  // 这里可以跳转到商户详情页面
  console.log('查看商户详情:', merchant)
}

// 复制回调地址
const copyCallbackUrl = async (url) => {
  if (!url) return
  
  try {
    await navigator.clipboard.writeText(url)
    ElMessage.success('回调地址已复制到剪贴板')
  } catch (error) {
    // 降级处理：使用传统方法
    const textArea = document.createElement('textarea')
    textArea.value = url
    document.body.appendChild(textArea)
    textArea.select()
    try {
      document.execCommand('copy')
      ElMessage.success('回调地址已复制到剪贴板')
    } catch (fallbackError) {
      ElMessage.error('复制失败，请手动复制')
    }
    document.body.removeChild(textArea)
  }
}


// 格式化时间
const formatTime = (timeString) => {
  if (!timeString) return '-'
  const date = new Date(timeString)
  return date.toLocaleString('zh-CN', {
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit'
  })
}

// 组件挂载
onMounted(() => {
  refreshMerchantStats()
  refreshNotifyLogs()
  refreshQueueStatus()
  
  // 设置定时刷新
  refreshTimer = setInterval(() => {
    refreshQueueStatus()
  }, 30000) // 30秒刷新一次
})

// 组件卸载
onUnmounted(() => {
  if (refreshTimer) {
    clearInterval(refreshTimer)
  }
})
</script>

